<!--直播预告-->
<template>
  <div class="living">
    <!-- 直播预告 -->
    <div class="live-ing">
      <div class="title">直播预告</div>
      <div class="live-wrap">
        <liveBroad :info="item" v-for="item in list" :key="item.id"></liveBroad>
      </div>
    </div>
    <el-pagination
      v-if="pagination.total/pagination.pageSize>1"
      style="text-align:center"
      @current-change="handleCurrentChange"
      :current-page="pagination.pageNo"
      :page-size="pagination.pageSize"
      layout="total, prev, pager, next, jumper"
      :total="pagination.total">
    </el-pagination>
  </div>
</template>

<script>
export default {
  components:{
    liveBroad:()=>import("@/components/live-broad")
  },
  data(){
    return {
      list:[],
      pagination:{
        total:0,
        pageSize: 12,
        pageNo:1
      }
    }
  },
  created(){
    this.getAptData();
  },
  methods:{
    getAptData(){
      const params = {
        pageNo:this.pagination.pageNo,
        pageSize:this.pagination.pageSize
      }
      this.$api.getAptData(params).then(res => {
        this.list = res.data;
        this.pagination.total = Number(res.totalDatas);
      })
    },
    handleCurrentChange(current){
      this.pagination.pageNo = current;
      this.getAptData()
    }
  }
}
</script>

<style lang="scss" scoped>
.living {padding: 32px;min-height: 100%;}
.live-ing >.title {color: #606266;line-height: 1;padding-bottom: 20px}
.live-ing .live-wrap {margin-left: -15px;margin-right: -15px;display: flex;flex-wrap: wrap;}
.live-ing .live-wrap > div {width: 25%;margin-bottom: 20px;}
</style>
<style>
.living .el-pagination .btn-prev {
  background: #F1F3F8;
}
.living .el-pagination .btn-next {
  background: #F1F3F8;
}
.living .el-pagination .el-pager li {
  background: #F1F3F8;
}
</style>